import axios from 'axios'
import React from 'react'
import { Button, Col, Container, Form, Row } from 'react-bootstrap'
import { useState } from 'react'
import { message } from 'antd'

const AttachPatientReport = () => {
  const [searchMobileNumber, setSearchMobileNumber] = useState('')
  const [patientId, setPatientId] = useState('')
  const [name, setName] = useState('')
  const [gender, setGender] = useState('')
  const [validated, setValidated] = useState(false)
  const [labReport, setLabReport] = useState('')
  const [reportName, setReportName] = useState('')
  const [fee, setFee] = useState('')
  const [paymentMode, setPaymentMode] = useState('Cash')
  const [requestedBy, setRequestedBy] = useState('')
  const [docNames, setDocNames] = useState([])

  const getDocNames = async () => {
    try {
      const res = await axios.get('/api/v1/receptionist/getDocNames')
      if (res.data.success) {
        console.log(res.data.data)
        setDocNames(res.data.data)
      } else {
        message.error('something went wrong')
      }
    } catch (error) {
      message.error('Could not find the Doctors')
      console.log(error)
    }
  }
  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader()
      fileReader.readAsDataURL(file)
      fileReader.onload = () => {
        resolve(fileReader.result)
      }
      fileReader.onerror = (error) => {
        reject(error)
      }
    })
  }

  const addLabReport = async (e) => {
    try {
      e.preventDefault()
      e.stopPropagation()
      const form = e.currentTarget
      if (form.checkValidity() === false) {
        e.preventDefault()
        e.stopPropagation()
      }
      setValidated(true)
      const data = {
        patientId,
        patientName: name,
        reportName: reportName,
        reportFile: labReport,
        fee,
        paymentMode,
        requestedBy,
      }

      const res = await axios.post('/api/v1/receptionist/addLabReport', data)
      if (res.data.success) {
        message.success('Lab Report Added Successfully')
      } else {
        message.error('something went wrong')
      }
    } catch (error) {
      message.error('Could not add the Lab Report')
      console.log(error)
    }
  }

  const getPatientData = async (e) => {
    try {
      e.preventDefault()
      e.stopPropagation()
      await getDocNames()
      const res = await axios.post('/api/v1/receptionist/getPatientData', { searchMobileNumber })
      if (res.data.success) {
        setPatientId(res.data.data.uniqueId)
        setName(res.data.data.name)
        setGender(res.data.data.gender)
        message.success('Patient Details Fetched Successfully')
      } else {
        message.error('something went wrong')
      }
    } catch (error) {
      message.error('Could not find the Patient')
      console.log(error)
    }
  }
  return (
    <>
      <>
        <Form autoComplete="off" onSubmit={getPatientData}>
          <Container>
            <Row className="mb-3">
              <Form.Group
                as={Col}
                className="d-flex offset-md-7 col-md-5 col-sm-12 col-sm-12"
                controlId="formGridEmail"
              >
                <Form.Control
                  type="text"
                  onChange={(e) => {
                    if (e.keyCode === 'enter') {
                      e.preventDefault()
                      e.stopPropagation()
                      if (e.target.value.length > 10) {
                        message.error('Mobile Number should be of 10 digits')
                        return
                      }
                      if (e.target.value.length < 10) {
                        message.error('Mobile Number should be of 10 digits')
                        return
                      }
                      getPatientData(e)
                      getDocNames()
                    }
                    setSearchMobileNumber(e.target.value)
                  }}
                  value={searchMobileNumber}
                  className="me-2"
                  placeholder="Patient Mobile Number"
                />
                <Button type="submit">Search</Button>
              </Form.Group>
            </Row>
          </Container>
        </Form>
      </>
      <Container>
        <Form
          noValidate
          validated={validated}
          onSubmit={addLabReport}
          autoComplete="off"
          encType="multipart/form-data"
        >
          <Row className="mb-3 col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12">
            <Form.Group as={Col} className="" controlId="formGridEmail">
              <Form.Label>Patient Id</Form.Label>
              <Form.Control type="text" placeholder="Patient Id" value={patientId} disabled />
            </Form.Group>
          </Row>

          <Row className="mb-3">
            <Form.Group
              as={Col}
              className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
              controlId="formFirstName"
            >
              <Form.Label>First Name</Form.Label>
              <Form.Control type="text" placeholder="  First Name" value={name} disabled />
            </Form.Group>

            <Form.Group
              as={Col}
              className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
              controlId="formGender"
            >
              <Form.Label>Gender</Form.Label>
              <Form.Control type="text" placeholder="Gender" value={gender} disabled />
            </Form.Group>
          </Row>
          <Row className="mb-3">
            <Form.Group
              as={Col}
              className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
              controlId="formFirstName"
            >
              <Form.Label>Request By</Form.Label>
              <Form.Select
                defaultValue="Choose..."
                onChange={(e) => {
                  setRequestedBy(e.target.value)
                }}
              >
                <option>Choose...</option>
                {docNames &&
                  docNames.length > 0 &&
                  docNames.map((docName, index) => {
                    return (
                      <option key={index} value={`${docName.name} ${docName.uniqueId}`}>
                        {`${docName.name} ${docName.uniqueId}`}
                      </option>
                    )
                  })}
              </Form.Select>
            </Form.Group>
            <Form.Group
              as={Col}
              className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
              controlId="formFirstName"
            >
              <Form.Label>Report Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Report Name"
                onChange={(e) => {
                  setReportName(e.target.value)
                }}
              />
            </Form.Group>
            <Form.Group
              as={Col}
              className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12 mb-3"
              controlId="formFirstName"
            >
              <Form.Label>Fee</Form.Label>
              <Form.Control
                type="number"
                placeholder="Fee"
                onChange={(e) => {
                  setFee(e.target.value)
                }}
              />
            </Form.Group>
            <Form.Group
              as={Col}
              className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
              controlId="formFirstName"
            >
              <Form.Label>Payment Mode</Form.Label>
              <Form.Select
                aria-label="Mode of payment"
                onChange={(e) => {
                  setPaymentMode(e.target.value)
                }}
              >
                <option disabled>Choose</option>
                <option value="Cash">Cash</option>
                <option value="UPI">UPI</option>
                <option value="Card">Card</option>
              </Form.Select>
            </Form.Group>
          </Row>
          <Row>
            <Form.Group
              controlId="formFile"
              className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12 mb-3"
            >
              <Form.Label>Upload patient lab reports here</Form.Label>
              <Form.Control
                type="file"
                className="b-1 border-dark"
                onChange={(e) => {
                  convertBase64(e.target.files[0]).then((res) => {
                    setLabReport(res)
                  })
                }}
              />
            </Form.Group>
            <Form.Group
              as={Col}
              className="col-sm-12 col-lg-3 col-xs-12 col-md-3 col-12 d-flex offset-md-5 offset-lg-5 offset-xs-0 mb-3"
              controlId="formGender"
            >
              <Button
                type="submit"
                className="w-100"
                style={{
                  alignSelf: 'flex-end',
                }}
                onClick={addLabReport}
              >
                Submit
              </Button>
            </Form.Group>
          </Row>
        </Form>
      </Container>
    </>
  )
}

export default AttachPatientReport
